.card_container{
    height: 260px;
    width: 180px;
    overflow: hidden;
    box-shadow: 1px 1px 1px 1px;
    border: 1px solid rgb(117, 117, 117);
    background: #fff;
    color: #000;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
}

.card-container{
    transition: all .3s ease-in-out;
}

.selected-card{
    transform: rotate(14deg) translateX(-20px) translateY(-25px);
}
.card_name{
    position: absolute;
    width: 40px;
}

.card_name_top{
    top: 12px;
    left: 12px;
}

.card_name_bottom{
    bottom: 12px;
    right: 12px;
    transform: rotate(180deg);
}

.card_value{
    text-align: center;
    font-size: 48px;
    color: #000;
    position: absolute;
    top: 50px;
    left: 15px;
}

.card_value_bottom{
    bottom: 50px;
    right: 18px;
    top: unset;
    left: unset;
    transform: rotate(180deg);
}

.cards_selector_container{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(50px, 1fr));
    gap: 10px;
    max-width: 35vw;
}

.cards_selector_table{
    display: flex;
    gap: 200px;
    padding: 60px;
}

.cards_selector_title{
    margin-bottom: 36px;
    display: block;
    margin-top: 70px;
}

.cards_selector_head{
    display: flex;
    justify-content: space-between;
    margin-top: 120px;
    margin-left: 60px;
    margin-right: 60px;
    flex-direction: column;
}

.cards_selector_head__link{
    cursor: pointer;
    user-select: none;
    font-size: 64px;
}

.cards-selector{
    max-width: 90vw;
}

.fields{
    display: flex;
    justify-content: space-between;
    margin-bottom: 60px;
}

.field{
    transition: all .3s ease-in-out;
    border: 1px solid #000;
    box-shadow: 1px 2px 1px 0px;
    border-radius: 12px;
}

.highlight{
    border: 1px solid #FFBF00;
    box-shadow: unset;
}

.field-cards{
    max-width: 360px;
    max-height: 260px;
    min-width: 360px;
    min-height: 260px;
    border-radius: 12px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(50px, 1fr));
    gap: 10px;
    font-size: 48px;
}

.fields_elements_container{
    display: flex;
    justify-content: space-between;
}

.fields{
    display: flex;
    flex-direction: column;
    gap: 36px;
    margin-bottom: 120px;
    margin-top: 24px;
}

.winPopup{
    position: fixed;
    top: 0;
    left: 0;
    font-size: 48px;
    display: flex;
    color: #e4d414;
    background: #000;
    padding: 12px 24px;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}


@media(max-width:1200px){
    @media(min-width:680px){
        .cards_selector_container_selected{
            max-width: 20vw;
        }
    }
}

@media(max-width:680px){
    .cards_selector_container{
        max-width: 70vw;
    }
    .cards_selector_container_selected{
      position: fixed;
      bottom: 0;
      display: flex;
      left: 0;
      overflow-x: scroll;
      width: 100vw;
      max-width: 100vw;
      transform: translateY(80px);
      background-color: #000;
      padding-top: 12px;
    }

    .cards_selector_title_selected{
        display: none;
    }

    .cards_selector_table{
        padding: 10px;
    }
  }

@media(max-width: 700px) {
    .cards_selector_container_layout{
        position: fixed;
        bottom: 0;
        display: flex;
        left: 0;
        overflow-x: scroll;
        width: 100vw;
        max-width: 100vw;
        transform: translateY(80px);
        background-color: #000;
        padding-top: 12px;
    }

    .fields_elements_container{
        overflow-x: scroll;
    }

    .fields{
        justify-content: unset;
        min-height: 120vh;
    }
}